/* eslint max-len: ["error", { "ignoreComments": true }] */
import {
  E_AUTH, E_CONNECTIVITY, E_UNEXPECTED, E_USER,
} from 'lib-bb-model-errors';

/**
 * @typedef {Object} errorMessageDescriptor
 * @property {string} message Localization key of the error message
 * @property {string} title Localization key of the error message`s title
 * @property {string} icon CSS class for icon
 */

/**
 * @description
 * Map of error code to message, title and icon to display to user
 *
 * @public
 * @name errors
 * @type {object.<E_AUTH | E_CONNECTIVITY | E_UNEXPECTED | E_USER, errorMessageDescriptor>}
 */
const errors = {
  [E_AUTH]: {
    message: 'model.error.auth.message',
    title: 'model.error.auth.title',
    icon: 'fa-sign-in',
  },
  [E_CONNECTIVITY]: {
    message: 'model.error.connectivity.message',
    title: 'model.error.connectivity.title',
    icon: 'fa-exclamation-triangle',
  },
  [E_USER]: {
    message: 'model.error.user.message',
    title: 'model.error.user.title',
    icon: 'fa-user',
  },
  [E_UNEXPECTED]: {
    message: 'model.error.unexpected.message',
    title: 'model.error.unexpected.title',
    icon: 'fa-server',
  },
};

export default ({ $filter, stateContainer }) => {
  const numberFilter = $filter('number');
  /**
   * @name currencyPair
   * @type {function}
   *
   * @description
   * Selector for currency pair data processing
   *
   * @returns {object} currency pair with rates data object
   */
  const currencyPair = stateContainer.createSelector(state => {
    const currencyPairData = {
      ...state.currencyPair.data,
      ...state.currencyRates.data,
    };
    return currencyPairData;
  });

  /**
   * @name accountsPayingFrom
   * @type {function}
   *
   * @description
   * Selector for accounts paying from data processing
   *
   * @returns {array} accounts paying from with account data
   */
  const accountsPayingFrom = stateContainer.createSelector(state => {
    const currencySell = state.currencyPair.data.pairKey.split('/')[0];
    return state.currencyPair.data.accounts
      .filter(account => account.balance.currencyCode === currencySell)
      .sort((item1, item2) => item2.balance.amount - item1.balance.amount);
  });

  /**
   * @name accountsPayingTo
   * @type {function}
   *
   * @description
   * Selector for accounts paying to data processing
   *
   * @returns {array} accounts paying to with account data
   */
  const accountsPayingTo = stateContainer.createSelector(state => {
    const currencyBuy = state.currencyPair.data.pairKey.split('/')[1];
    return state.currencyPair.data.accounts
      .filter(account => account.balance.currencyCode === currencyBuy)
      .sort((item1, item2) => item2.balance.amount - item1.balance.amount);
  });

  /**
   * @description
   * Checks if currency pair data is available
   *
   * @name isCurrencyPairAvailable
   * @type {function}
   * @param {object} currencyPair
   * @returns {boolean} returns true if currency pair data is not present
   */
  const isCurrencyPairAvailable = stateContainer.createSelector(
    state => Object.keys(state.currencyPair.data).length
  );

  /**
   * @description
   * Returns error object by error code
   *
   * @public
   * @name getError
   * @type {function}
   */
  const getError = (code) => errors[code];

  /**
   * @description
   * Appends + or - sign if number is positive or negative
   *
   * @public
   * @name showSign
   * @type {function}
   * @param {number} number     Number inputed
   * @param {number} decimals   Number of decimals
   *
   * @returns {string}          String with appended sign and number
   */
  const showSign = (number, decimals) => {
    const absNumber = decimals
      ? Math.abs(number).toFixed(decimals) : Math.abs(number);
    const sign = number > 0 ? '+' : '-';
    const localizedNumber = decimals
      ? numberFilter(absNumber, decimals) : absNumber;
    return `${sign} ${localizedNumber}`;
  };

  return {
    currencyPair,
    isCurrencyPairAvailable,
    accountsPayingFrom,
    accountsPayingTo,
    getError,
    showSign,
  };
};




